import {
  GraphModel,
  Node,
  NodeContext,
  NodeDefinition,
  Model,
  EditorConnection,
} from "@fluxscape/runtime";

interface Internal {
  inputValues: Record<string, any>;
  dirtyValues: Record<string, boolean>;
  model: any;
  onModelChangedCallback: (args: { name: string }) => void;
}

export const node: NodeDefinition<Internal> = {
  name: "net.noodl.ComponentObject",
  displayNodeName: "Component Object",
  category: "Component Utilities",
  color: "component",
  docs: "https://docs.noodl.net/nodes/component-utilities/component-object",
  initialize() {
    // @ts-expect-error
    this._internal = {
      inputValues: {},
      dirtyValues: {},
      onModelChangedCallback: (args) => {
        if (this.isInputConnected("fetch") !== false) return;

        if (this.hasOutput("value-" + args.name)) {
          this.flagOutputDirty("value-" + args.name);
        }

        if (this.hasOutput("changed-" + args.name)) {
          this.sendSignalOnOutput("changed-" + args.name);
        }

        this.sendSignalOnOutput("changed");
      },
    };

    const model = Model.get(
      // @ts-expect-error
      "componentState" + this.nodeScope.componentOwner.getInstanceId()
    );
    this._internal.model = model;

    model.on("change", this._internal.onModelChangedCallback);
  },
  getInspectInfo() {
    return {
      type: "value",
      value: this._internal.model.data,
    };
  },
  inputs: {
    properties: {
      type: {
        name: "stringlist",
        allowEditOnly: true,
      },
      displayName: "Properties",
      group: "Properties",
      set(value: any) {},
    },
    fetch: {
      displayName: "Fetch",
      group: "Actions",
      valueChangedToTrue() {
        // @ts-expect-error
        this.scheduleFetch();
      },
    },
  },
  outputs: {
    changed: {
      type: "signal",
      displayName: "Changed",
      group: "Events",
    },
    fetched: {
      type: "signal",
      displayName: "Fetched",
      group: "Events",
    },
  },
  methods: {
    scheduleStore() {
      // @ts-expect-error
      if (this.hasScheduledStore) return;
      // @ts-expect-error
      this.hasScheduledStore = true;

      const internal = this._internal;
      this.scheduleAfterInputsHaveUpdated(() => {
        // @ts-expect-error
        this.hasScheduledStore = false;
        for (const i in internal.dirtyValues) {
          internal.model.set(i, internal.inputValues[i], { resolve: true });
        }
        internal.dirtyValues = {};
      });
    },
    scheduleFetch() {
      // @ts-expect-error
      if (this.hasScheduledFetch) return;
      // @ts-expect-error
      this.hasScheduledFetch = true;

      this.scheduleAfterInputsHaveUpdated(() => {
        // @ts-expect-error
        this.hasScheduledFetch = false;
        // @ts-expect-error
        this.fetch();
      });
    },
    fetch() {
      for (const key in this._internal.model.data) {
        if (this.hasOutput("value-" + key)) {
          this.flagOutputDirty("value-" + key);
          if (this.hasOutput("changed-" + key)) {
            this.sendSignalOnOutput("changed-" + key);
          }
        }
      }
      this.sendSignalOnOutput("fetched");
    },
    _onNodeDeleted() {
      Node.prototype._onNodeDeleted.call(this);
      this._internal.model.off("change", this._internal.onModelChangedCallback);
    },
    registerOutputIfNeeded(name: string) {
      if (this.hasOutput(name)) {
        return;
      }

      const split = name.split("-");
      const propertyName = split[split.length - 1];

      this.registerOutput(name, {
        get() {
          return this._internal.model.get(propertyName, { resolve: true });
        },
      });
    },
    registerInputIfNeeded: function (name: string) {
      if (this.hasInput(name)) {
        return;
      }

      if (name.startsWith("value-")) {
        const propertyName = name.substring("value-".length);
        this.registerInput(name, {
          set(value: any) {
            this._internal.inputValues[propertyName] = value;
            this._internal.dirtyValues[propertyName] = true;

            // @ts-expect-error
            this.scheduleStore();
          },
        });
      }
    },
  },
};

function updatePorts(
  nodeId: string,
  parameters: any,
  editorConnection: EditorConnection
) {
  const ports: any[] = [];

  // Add value outputs
  if (parameters.properties) {
    const properties = parameters.properties.split(",");
    for (const p of properties) {
      ports.push({
        type: {
          name: "*",
          allowConnectionsOnly: true,
        },
        plug: "input/output",
        group: "Properties",
        name: "value-" + p,
        displayName: p,
      });

      ports.push({
        type: "signal",
        plug: "output",
        group: "Changed Events",
        displayName: p + " Changed",
        name: "changed-" + p,
      });
    }
  }

  editorConnection.sendDynamicPorts(nodeId, ports, {
    detectRenamed: {
      plug: "input/output",
    },
  });
}

export function setup(context: NodeContext, graphModel: GraphModel) {
  if (
    !context.editorConnection ||
    !context.editorConnection.isRunningLocally()
  ) {
    return;
  }

  graphModel.on("nodeAdded.net.noodl.ComponentObject", (node: any) => {
    updatePorts(node.id, node.parameters, context.editorConnection!);

    node.on("parameterUpdated", (event: any) => {
      if (event.name === "properties") {
        updatePorts(node.id, node.parameters, context.editorConnection!);
      }
    });
  });
}
