import {
  EditorConnection,
  GraphModel,
  Model,
  NodeContext,
  NodeDefinition,
} from "@fluxscape/runtime";

export interface SetComponentObjectState {
  inputValues: Record<string, any>;
  hasScheduledStore: boolean;
}

export function extendSetComponentObjectProperties(
  def: NodeDefinition<SetComponentObjectState> & {
    getComponentObjectId: () => string | undefined;
  }
) {
  const SetComponentObjectProperties: NodeDefinition<SetComponentObjectState> =
    {
      name: def.name,
      displayNodeName: def.displayName,
      category: "Component Utilities",
      color: "component",
      docs: def.docs,
      initialize: function () {
        this._internal.inputValues = {};
      },
      inputs: {
        properties: {
          type: {
            name: "stringlist",
            allowEditOnly: true,
          },
          displayName: "Properties",
          group: "Properties",
          set(value) {},
        },
        store: {
          type: "signal",
          group: "Actions",
          displayName: "Do",
          valueChangedToTrue() {
            // @ts-expect-error
            this.scheduleStore();
          },
        },
      },
      outputs: {
        stored: {
          type: "signal",
          group: "Events",
          displayName: "Done",
        },
      },
      methods: {
        getComponentObjectId: def.getComponentObjectId,
        scheduleStore() {
          // @ts-expect-error
          if (this.hasScheduledStore) return;
          // @ts-expect-error
          this.hasScheduledStore = true;

          const internal = this._internal;
          this.scheduleAfterInputsHaveUpdated(() => {
            // @ts-expect-error
            const model = Model.get(this.getComponentObjectId());
            // @ts-expect-error
            this.hasScheduledStore = false;

            // @ts-expect-error
            const properties = this.model.parameters.properties || "";
            const validProperties = properties.split(",");

            const keysToSet = Object.keys(internal.inputValues).filter(
              (key) => validProperties.indexOf(key) !== -1
            );

            for (const i of keysToSet) {
              model.set(i, internal.inputValues[i], { resolve: true });
            }
            this.sendSignalOnOutput("stored");
          });
        },
        registerInputIfNeeded: function (name: string) {
          if (this.hasInput(name)) {
            return;
          }

          if (name.startsWith("prop-")) {
            const propertyName = name.substring("prop-".length);
            this.registerInput(name, {
              set(value) {
                this._internal.inputValues[propertyName] = value;
              },
            });
          } else if (name.startsWith("type-")) {
            this.registerInput(name, {
              set(value) {},
            });
          }
        },
      },
    };

  function updatePorts(
    nodeId: string,
    parameters: any,
    editorConnection: EditorConnection
  ) {
    const ports: any[] = [];

    const _types = [
      { label: "String", value: "string" },
      { label: "Boolean", value: "boolean" },
      { label: "Number", value: "number" },
      { label: "Date", value: "date" },
      { label: "Array", value: "array" },
      { label: "Object", value: "object" },
      { label: "Any", value: "*" },
    ];

    // Add value outputs
    let properties = parameters.properties;
    if (properties) {
      properties = properties ? properties.split(",") : undefined;
      for (const p of properties) {
        // Property input
        ports.push({
          type: {
            name:
              parameters["type-" + p] === undefined
                ? "*"
                : parameters["type-" + p],
          },
          plug: "input",
          group: "Property Values",
          displayName: p,
          name: "prop-" + p,
        });

        // Property type
        ports.push({
          type: {
            name: "enum",
            enums: _types,
            allowEditOnly: true,
          },
          plug: "input",
          group: "Property Types",
          displayName: p,
          default: "*",
          name: "type-" + p,
        });
      }
    }

    editorConnection.sendDynamicPorts(nodeId, ports, {
      detectRenamed: {
        plug: "input",
      },
    });
  }

  return {
    node: SetComponentObjectProperties,
    setup(context: NodeContext, graphModel: GraphModel) {
      if (
        !context.editorConnection ||
        !context.editorConnection.isRunningLocally()
      ) {
        return;
      }

      graphModel.on("nodeAdded." + def.name, (node: any) => {
        updatePorts(node.id, node.parameters, context.editorConnection!);

        node.on("parameterUpdated", (event: any) => {
          if (event.name === "properties" || event.name.startsWith("type-")) {
            updatePorts(node.id, node.parameters, context.editorConnection!);
          }
        });
      });
    },
  };
}
